.title {
    font-size: 30px;
        text-transform: uppercase;
        font-weight: 700;
        margin-bottom: 50px;
        
}

.form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-top: 50px;
    padding-left: 40px;
    padding-right: 40px;
    padding-bottom: 50px;
    background-color: rgb(217, 219, 222);
}

.label {
    display: block;
    margin-bottom: 10px;
    font-size: 20px;
    text-transform: uppercase;
    font-weight: 700;
}

.input {
    width: 400px;
    padding: 10px;
    margin-bottom: 40px;
    font-size: 24px;
}

.container {
    padding-top: 30px;
    padding-left: 15px;
    padding-right: 15px;
    display: flex;
    max-width: 1280px;
    justify-content: space-between;
    margin-left: auto;
    margin-right: auto;
    background-color:azure;
}